<template>
	<div class="qyjs">
		<transition name="scale"><div class="logo" v-show="isLogoShow">
			<img src="../assets/main_logo_m.png"/>
		</div></transition>
		<transition name="translate"><div class="ins" v-show="isInShow">
			<p>2002518""800</p>
			<p></p>
			<p></p>
		</div></transition>
		<transition name="fade"><div class="foot" v-show="isfootShow">
			<img src="../assets/qyjs_foot.png"/>
		</div></transition>
	</div>
</template>

<script>

	export default {
		name: 'qyjs',
		data() {
			return {
				isLogoShow: false,
				isInShow: false,
				isfootShow: false
			}
		},
		mounted() {
			setTimeout(() => {
				this.isLogoShow = true
			}, 300)

			setTimeout(() => {
				this.isInShow = true
			}, 800)

			setTimeout(() => {
				this.isfootShow = true
			}, 1000)
		}
	}
</script>

<style scoped>
	.qyjs {
		height: calc(100vh - 55px);
		background: url('../assets/qyjs_bg.jpg') no-repeat center;
		background-size: 100% 100%;

	}
	.logo {
		width: 25%;
		position: absolute;
		top: 50px;
		left: 50%;
		transform: translateX(-50%);
	}
	.ins {
		position: absolute;
		top: 25%;
		color: #331e17;
		font-size: 10px;
		padding: 0 20px;
	}
	.ins p {
		margin-bottom: 20px;
		text-indent: 20px;
	}
	.foot {
		position: absolute;
		width: 30%;
		left: 50%;
		transform: translateX(-50%);
		bottom: 40px;
	}
</style>


